/* App.css */

/* Define a class for the header */
.header {
  background-color: #001529; /* Set your desired background color */
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Style for the logo */
.logo {
  width: 120px; /* Adjust width as needed */
  height: 31px; /* Adjust height as needed */
  background-size: contain;
  margin: 16px; /* Adjust margin as needed */
}

/* Style for the menu */
.menu {
  display: flex;
  align-items: center;
}

/* Media query for responsive design */
@media screen and (max-width: 768px) {
  /* Adjust styles for smaller screens or mobile devices */
  .logo {
    width: 100px; /* Adjust width for smaller screens */
    height: 25px; /* Adjust height for smaller screens */
  }
}
